import React, { Component } from 'react';
import { autorun } from "mobx"
import { teams } from "../lib/gameInfo"

function rect(props) {
    const {ctx, x, y, width, height} = props;
    ctx.fillRect(x, y, width, height);
}

class CanvasShootingLevels extends Component {
    componentDidMount() {
        this.componentDidUpdate()
    }

    drawArc(topLeft, topRight, c, percent) {
        c.translate(topLeft, topRight)
        c.beginPath();
        c.strokeStyle = '#444';
        c.lineWidth = '2';
        c.arc(0, 0, 4, 0, Math.PI * 2 * 1)
        c.stroke();
        c.setLineDash([])
        c.rotate((-1 / 2) * Math.PI)
        c.beginPath();
        c.arc(0, 0, 4, 0, Math.PI * 2 * percent)
        c.strokeStyle = 'yellow';
        c.lineWidth = '2';
        c.stroke();
        c.setTransform(1, 0, 0, 1, 0, 0);
    }

    componentDidUpdate() {
        if (this.disposer) this.disposer();
        this.disposer = autorun(() => {
            const ctx = this.refs.canvas.getContext('2d');
            ctx.fillStyle = "white"
            ctx.clearRect(0, 0, this.props.width, this.props.height);
            this.props.units.forEach(unit => {
                const { x, y, shootingLevel, unitType: {shootingLevelXOffset} } = unit
                if(!shootingLevel || shootingLevel===0) return
                this.drawArc(x+shootingLevelXOffset, y-30,ctx, shootingLevel/100)
                ctx.setTransform(1, 0, 0, 1, 0, 0);
            })
        })
    }

    render() {
        const { width, height } = this.props
        return (
            <canvas ref="canvas" className="shootingLevelCanvas" width={width} height={height} ></canvas>
        );
    }
}

export default CanvasShootingLevels


